@import url(https://fonts.googleapis.com/css2?family=Tangerine:wght@400;700&display=swap);@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400;500;600;700&family=Montserrat:wght@400;500&display=swap);.header,.nav,.titre-app{display:flex}.header{background:linear-gradient(45deg,#000,#d3d3d3);padding-left:4%;padding-right:4%;width:92%;height:100px;flex-direction:row;align-items:center;justify-content:space-between;border-bottom:1px solid #000}.titre-app{width:40%;height:80px;align-items:center}.header_link{text-decoration:none;text-align:center;font-family:Tangerine;line-height:30px;font-weight:700;color:#fff;font-size:30px}.header_link:hover{color:#d3d3d3}.nav{flex-direction:row;align-items:center;justify-content:space-between}.nav_item1,.nav_item2,.nav_item3{position:relative;text-decoration:none}.nav_item3{width:15%}.nav_item2{width:56%}.nav_item1{width:25%}.nav_item1:hover,.nav_item2:hover,.nav_item3:hover{text-decoration:underline black}.nav_item_text1,.nav_item_text2,.nav_item_text3{font-family:Cormorant;color:#000;font-size:15px;width:50px;text-align:center}.nav_item_text2{width:107px}.nav_item_text3{width:28px}@media screen and (max-width:768px){.header{height:180px;flex-direction:column;justify-content:center;align-items:center;overflow:hidden}.header_link{display:flex;flex-direction:row;justify-content:center;font-size:30px;text-align:center}.titre-app{height:30px;width:100%}.nav_item_text1,.nav_item_text2,.nav_item_text3{color:#fff}}