@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400;500;600;700&family=Montserrat:wght@400;500&display=swap);.banner,.tof{overflow:hidden;position:relative}.banner{display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;background:linear-gradient(45deg,#fff,#d3d3d3);box-shadow:0 10px 30px 5px rgba(0,0,0,.2);left:0;width:80%;height:200px;border-radius:5px;z-index:2}.tof{border:2px solid #000;width:150px;height:150px;border-radius:50%}.profil-banner{width:100%;height:100%;border-radius:50%;object-fit:cover}.about-me{display:flex;flex-direction:column;justify-content:space-around;border-radius:5px;width:60%;height:80%}.txt-visit,.txt-welcome,.welcome{position:relative;margin:0}@media screen and (max-width:768px){.banner{flex-direction:column;height:500px;width:100%;padding-top:10%;position:relative;margin-top:0;border-radius:0}.txt-visit,.txt-welcome,.welcome{text-align:center}}